import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Card, Spin, Alert, Row, Col, Typography, Divider } from 'antd';
import moment from 'moment';
import './PlaceDetail.css';

const { Title, Text } = Typography;

interface WeatherDataEntry {
  datetime: string;
  temperature_celsius?: number;
  relative_humidity_percent?: number;
  wind_speed_m_s?: number;
  wind_direction?: string;
  wind_beaufort_scale?: number;
  total_precipitation_mm?: number;
  storm_probability_percent?: number;
  total_cloud_cover_percent?: number;
  pressure_hPa?: number;
  weather_icon?: string; // Added for weather icons
  [key: string]: any;
}

interface PlaceData {
  name: string;
  description: string | null;
  latitude: number;
  longitude: number;
  elevation: number;
  weather_data: WeatherDataEntry[];
}

const PlaceDetail: React.FC = () => {
  const {
    continentSlug,
    countrySlug,
    regionSlug,
    municipalitySlug,
    placeSlug,
  } = useParams<{
    continentSlug: string;
    countrySlug: string;
    regionSlug: string;
    municipalitySlug: string;
    placeSlug: string;
  }>();

  const [placeData, setPlaceData] = useState<PlaceData | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    const apiUrl = `/api/${countrySlug}/${regionSlug}/${municipalitySlug}/${placeSlug}/`;

    fetch(apiUrl)
      .then((response) => {
        if (!response.ok) {
          throw new Error('Failed to fetch place details');
        }
        return response.json();
      })
      .then((data) => {
        setPlaceData(data);
        setLoading(false);
      })
      .catch((error) => {
        setError(error.message);
        setLoading(false);
      });
  }, [countrySlug, regionSlug, municipalitySlug, placeSlug]);

  if (loading) {
    return <Spin tip="Loading..." />;
  }

  if (error) {
    return <Alert message="Error" description={error} type="error" showIcon />;
  }

  if (!placeData) {
    return null;
  }

  // Helper function to get weather icon based on condition
  const getWeatherIcon = (condition: string) => {
    // Map condition to icon filenames
    const iconMap: { [key: string]: string } = {
      'Clear': 'sunny',
      'Partly Cloudy': 'partly_cloudy',
      'Cloudy': 'cloudy',
      'Rain': 'rain',
      'Snow': 'snow',
      'Thunderstorm': 'thunderstorm',
      // Add more mappings as needed
    };

    const iconName = iconMap[condition] || 'default';
    return `/icons/${iconName}.png`; // Assuming icons are stored in public/icons/
  };

  // Process weather data
  const now = moment();

  // Current weather
  const currentWeather = placeData.weather_data.find((entry) => {
    const entryTime = moment(entry.datetime);
    return entryTime.isSameOrAfter(now);
  }) || placeData.weather_data[0];

  // Next 48 hours data
  const next48HoursData = placeData.weather_data.filter((entry) => {
    const entryTime = moment(entry.datetime);
    return entryTime.isSameOrAfter(now) && entryTime.isBefore(now.clone().add(48, 'hours'));
  });

  // Group data by date for daily summaries
  const dailyDataMap = new Map<string, WeatherDataEntry[]>();

  next48HoursData.forEach((entry) => {
    const date = moment(entry.datetime).format('YYYY-MM-DD');
    if (!dailyDataMap.has(date)) {
      dailyDataMap.set(date, []);
    }
    dailyDataMap.get(date)!.push(entry);
  });

  // Prepare daily summaries
  interface DailySummary {
    date: string;
    icon: string;
    minTemperature?: number;
    maxTemperature?: number;
    totalPrecipitation?: number;
    condition: string;
    [key: string]: any;
  }

  const dailySummaries: DailySummary[] = [];

  dailyDataMap.forEach((entries, date) => {
    const temperatures = entries
      .map((e) => e.temperature_celsius)
      .filter((t) => t !== undefined) as number[];
    const precipitations = entries
      .map((e) => e.total_precipitation_mm)
      .filter((p) => p !== undefined) as number[];

    const minTemperature = temperatures.length ? Math.min(...temperatures) : undefined;
    const maxTemperature = temperatures.length ? Math.max(...temperatures) : undefined;
    const totalPrecipitation = precipitations.length
      ? precipitations.reduce((a, b) => a + b, 0)
      : undefined;

    // Assume the most frequent condition for the day's icon
    const conditionCounts: { [key: string]: number } = {};
    entries.forEach((e) => {
      const condition = e.weather_icon || 'Default';
      conditionCounts[condition] = (conditionCounts[condition] || 0) + 1;
    });
    const condition = Object.keys(conditionCounts).reduce((a, b) =>
      conditionCounts[a] > conditionCounts[b] ? a : b,
    );

    dailySummaries.push({
      date,
      icon: getWeatherIcon(condition),
      minTemperature,
      maxTemperature,
      totalPrecipitation,
      condition,
    });
  });

  return (
    <div className="place-detail-container">
      <Card className="place-detail-card">
        <Row justify="space-between" align="middle">
          <Col>
            <Title level={2}>{placeData.name}</Title>
            {placeData.description && <Text>{placeData.description}</Text>}
            <br />
            <Text>
              <strong>Location:</strong> {placeData.latitude}, {placeData.longitude}
            </Text>
            <br />
            <Text>
              <strong>Elevation:</strong> {placeData.elevation} meters
            </Text>
          </Col>
          <Col>
            <img
              src={getWeatherIcon(currentWeather.weather_icon || 'Default')}
              alt="Current Weather Icon"
              className="current-weather-icon"
            />
          </Col>
        </Row>
        <Divider />
        <Row align="middle">
          <Col>
            <Title level={3}>{currentWeather.temperature_celsius}C</Title>
            <Text>{currentWeather.weather_icon || 'Current Condition'}</Text>
          </Col>
        </Row>
      </Card>

      <Card title="Hourly Forecast" className="hourly-forecast-card">
        <div className="hourly-forecast-container">
          <Row gutter={16}>
            {next48HoursData.map((entry) => (
              <Col key={entry.datetime} className="hourly-forecast-item">
                <Card bordered={false} className="hourly-card">
                  <Text>{moment(entry.datetime).format('HH:mm')}</Text>
                  <br />
                  <img
                    src={getWeatherIcon(entry.weather_icon || 'Default')}
                    alt="Weather Icon"
                    className="weather-icon"
                  />
                  <br />
                  <Text>{entry.temperature_celsius}C</Text>
                </Card>
              </Col>
            ))}
          </Row>
        </div>
      </Card>

      <Card title="Daily Forecast" className="daily-forecast-card">
        <Row gutter={[16, 16]}>
          {dailySummaries.map((summary) => (
            <Col key={summary.date} xs={24} sm={12} md={8} lg={6}>
              <Card bordered={false} className="daily-card">
                <Text strong>{moment(summary.date).format('dddd, MMM D')}</Text>
                <br />
                <img
                  src={summary.icon}
                  alt="Weather Icon"
                  className="weather-icon"
                />
                <br />
                <Text>
                  {summary.minTemperature}C - {summary.maxTemperature}C
                </Text>
                <br />
                <Text>{summary.condition}</Text>
              </Card>
            </Col>
          ))}
        </Row>
      </Card>
    </div>
  );
};

export default PlaceDetail;
